import * as React from 'react';
import { graphql } from 'gatsby';
import { MDXProvider } from '@mdx-js/react';
import { MDXRenderer } from 'gatsby-plugin-mdx';
import MdxLink from '../i18n/components/mdxLink';
import Layout from '~components/Layout/Template/Layout';

//import PageLayout from '~components/Layout/Templates/PageLayout/PageLayout';
//import Default from '~components/Layout/Templates/Default';
// The normal <a> tag is modified here (so that internal links use gatsby-link/LocalizedLink
// More info:
// https://www.gatsbyjs.org/docs/mdx/customizing-components/

const PageTemplate = ({ data }) => {
	const { page } = data;
	return (
		<>
			<Layout template="page" data={data}>
				<div className="inner">
					<article className="post-full post no-image page no-image">
						<header className="post-full-header">
							<h1 className="post-full-title">{page.frontmatter.title}</h1>
						</header>
						<section className="post-full-content">
							<div className="post-content">
								<MDXProvider
									components={{
										a: MdxLink,
									}}
								>
									<MDXRenderer>{page.body}</MDXRenderer>
								</MDXProvider>
							</div>
						</section>
					</article>
				</div>
			</Layout>
		</>
	);
};

export default PageTemplate;

export const PageTemplateQuery = graphql`
	query Page($title: String!, $locale: String!) {
		page: mdx(
			frontmatter: { title: { eq: $title } }
			fields: { locale: { eq: $locale } }
		) {
			frontmatter {
				title
			}
			body
		}
	}
`;
